import React, { Component } from "react";
import { Input, Button, Table } from "antd";
import { connect } from "react-redux";
import { userlist, removeuserlist, adduserlist } from "../../utils/request";
import { addActionList } from "../../store/actions/person";

class Person extends Component {
  state = {
    columns: [
      { title: "姓名", dataIndex: "acount", key: "acount" },
      { title: "年龄", dataIndex: "password", key: "password" },
      {
        title: "操作",
        dataIndex: "tags",
        key: "tags",
        render: (text, record, index) => (
          <>
            <Button
              type="primary"
              danger
              onClick={this.removeUser(text, record, index)}
            >
              删除
            </Button>
          </>
        ),
      },
    ],
    data: [],
    form: {
      acount: "",
      password: "",
      head_thumbnail: "24598",
    },
  };

  componentDidMount() {
    this.onQuery();
  }

  onQuery = () => {
    userlist().then((res) => {
      this.setState({
        data: res,
      });
      this.props.addActionList(res);
    });
  };
  changeAcount = (e) => {
    this.setState({
      form: {
        ...this.state.form,
        [e.target.name]: e.target.value,
      },
    });
  };
  removeUser = (text, record, index) => {
    return () => {
      removeuserlist(record.id).then((res) => {
        this.onQuery();
      });
    };
  };
  adduserlist = () => {
    adduserlist(this.state.form).then((res) => {
      this.onQuery();
      this.setState({
        form: {
          acount: "",
          password: "",
        },
      });
    });
  };
  render() {
    return (
      <div id="person">
        <header>
          <h1>Person</h1>
          <div>
            <Input
              placeholder="请输入成员名称"
              name="acount"
              value={this.state.form.acount}
              onChange={this.changeAcount}
            ></Input>
            <Input
              placeholder="请输入成员年龄"
              name="password"
              value={this.state.form.password}
              onChange={this.changeAcount}
            ></Input>
            <Button onClick={this.adduserlist}>添加新成员</Button>
          </div>
        </header>
        <article>
          <section>
            <Table
              columns={this.state.columns}
              dataSource={this.state.data}
              size="small"
              rowKey="id"
            ></Table>
          </section>
        </article>
      </div>
    );
  }
}
export default connect((state) => ({ count: state.reducer.count }), {
  addActionList,
})(Person);
