import axios from "axios";

const baseUrl = "/api";

// axios请求拦截器
axios.interceptors.request.use((request) => {
  return request;
});
// axios响应拦截器
axios.interceptors.response.use((response) => {
  return response.data;
});

export const userlist = () => {
  return axios({
    url: "/user/findAll",
    method: "GET",
  });
};
export const adduserlist = (data) => {
  return axios({
    url: "/user/create",
    method: "POST",
    data
  });
};
export const removeuserlist = (data) => {
  return axios({
    url: "/user/remove",
    method: "DELETE",
    data:{
      id:data
    }
  });
};
