import React, { Component } from "react";
import { connect } from "react-redux";
import {
  addAction,
  subAction,
  addAsyncAction,
} from "../../store/actions/count";

class ReduxHome extends Component {
  state = {
    num: 1,
  };

  // 改变select的值
  changeSelect = (e) => {
    this.setState({
      num: e.target.value,
    });
  };

  // 增加
  add = () => {
    this.props.addAction(this.state.num);
  };

  // 减少
  subtract = () => {
    if (this.props.count - this.state.num >= 0)
      this.props.subAction(this.state.num);
  };

  // 奇数相加
  oddNumber = () => {
    // // 判断count是否为奇数，是则相加，否则不做任何操作
    if (this.props.count % 2 === 1) {
      this.props.addAction(this.state.num);
    }
  };

  // 异步相加
  asyncAdd = () => {
    this.props.addAsyncAction(this.state.num, 500);
  };

  render() {
    const { num } = this.state;
    const { count } = this.props;
    return (
      <div>
        <h3>当前求和为: {count}</h3>
        <div>
          <select
            name=""
            id=""
            style={{ width: "50px" }}
            value={num}
            onChange={this.changeSelect}
          >
            <option value={1}>1</option>
            <option value={2}>2</option>
            <option value={3}>3</option>
            <option value={4}>4</option>
          </select>
          <button onClick={this.add}>+</button>
          <button onClick={this.subtract}>-</button>
          <button onClick={this.oddNumber}>和为奇数相加</button>
          <button onClick={this.asyncAdd}>异步相加</button>
        </div>
      </div>
    );
  }
}

