// action.js
import { ADD_COUNT, SUB_COUNT } from "../constants";

export const addAction = (num) => {
  // 普通异步action会报错，因为这里的action不是一个对象
  //   setTimeout(() => {
  return {
    type: ADD_COUNT,
    text: num,
  };
  //   },200);
};
export const subAction = (num) => {
  return {
    type: SUB_COUNT,
    text: num,
  };
};

// 异步action的实现。他的值是函数,异步action中一般会调用同步action，一般请求
export const addAsyncAction = (num,time)=>{
    return (dispatch)=>{
        setTimeout(() => {
            dispatch(addAction(num));
        },time)
    }
}
